import Home from '../views/Home.vue';
import {
  createRouter,
  createWebHistory,
  createWebHashHistory
} from 'vue-router';

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
]


// 
const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

export default router;